@import url(https://fonts.googleapis.com/css2?family=Titan+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&family=Titan+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Candal&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&family=Titan+One&display=swap);
body {
  position: relative;
  margin: 0;
  display:flex;
  height:100vh;
  flex-direction: column wrap;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;



}


*, ::before,::after{
  box-sizing:border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
#root{
  height:100%;
  width:100%;
  position:relative;
  scroll-behavior: smooth;
  overflow-y: scroll;
  -webkit-scroll-snap-points-y:repeat(100vh);
      -ms-scroll-snap-points-y:repeat(100vh);
          scroll-snap-points-y:repeat(100vh);
  -webkit-scroll-snap-type: y mandatory;
      -ms-scroll-snap-type: y mandatory;
          scroll-snap-type: y mandatory;
}
.App{
  position:relative;
  display:flex;
  justify-content: flex-start;
  min-height: 100vh;
  flex-wrap: wrap;
  width:100%;
  scroll-snap-align: center;

}

@media (max-device-width: 800px){
  .App{
    scroll-snap-align: none;
  }
 
}

#root{
  height:100vh;
}


.FrontPage{
    background-color:whitesmoke 0.6;
    width:30%;
    display:flex;
    flex-direction: column;
    justify-content: space-evenly;
    height:100vh;
}

@media (max-device-width: 800px){

.FrontPage{
    width:100vw;
    height:100vh;
    background-image: url(/portfolio/static/media/Zinc.02150daf.jpg);
    background-repeat: no-repeat;
}

.Photo{
 display: none;   
}
  
}

.Intro{
    
    text-align: center;
    flex-wrap: wrap;
}
.Intro h1{
    font-family: 'Titan One', cursive;
    letter-spacing: 0.2rem;
    background: -webkit-linear-gradient(#06dce4, #03108d);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
.Intro h3{
    font-family:'Roboto', sans-serif;
    padding:0.5rem;
    color:rgb(68, 61, 61);
    
}


@media (max-device-width: 800px){

    .Intro h1,h3{
        
        padding:1.3rem;
        text-align:center;
    }
    
}
.Photo img{
    border-radius:50%;
    width:50%;
    height:86%;
     display: block;
    margin-left: auto;
    margin-right: auto;
    box-shadow: 0 19px 38px rgba(0,0,0,0.30), 0 15px 12px rgba(0,0,0,0.22);
    /* flex-grow:1; */
}


.Social{
    display:flex;
    flex-direction: row;
    justify-content: space-evenly;
    flex-wrap:wrap;
   
}

.Social a{
    color:inherit;
    text-decoration: none;
}

#fb,#twi,#git,#in,#mail{
    -webkit-filter:drop-shadow(5px 2px 1px rgba(128, 128, 128, 0.6));
            filter:drop-shadow(5px 2px 1px rgba(128, 128, 128, 0.6));
    
}

#fb:hover{
    color:#3b5998;
    cursor:pointer;
    transform: perspective(100px) translateZ(20px);
    transition: linear 400ms;
  
    
}
#twi:hover{
    color:#00acee;
    cursor:pointer;
    transform: perspective(100px) translateZ(20px);
    transition: linear 400ms;
   
    
}
#in:hover{
    color:#0e76a8 ;
    cursor:pointer;
    transform: perspective(100px) translateZ(20px);
    transition: linear 400ms;
  
    
}
#git:hover{
    color:#AD5C51;
    cursor:pointer;
    transform: perspective(100px) translateZ(20px);
    transition: linear 350ms;
    
}

#mail:hover{
    cursor:pointer;
    transform: perspective(100px) translateZ(20px);
    transition: linear 350ms;
}

@media (max-device-width: 800px){

    .Social{
        flex-direction: column;
        justify-content: space-evenly;
        font-size:80%;
        height:400px;
        background-image: url(/portfolio/static/media/crop2.b7524095.png);
        background-repeat: no-repeat;
        background-position: 90% 80%;
        
    
    
    }
    #mail{
        display:none;
    }

    
    }
.Contact{
    display:flex;
    height: 100vh;
    flex-flow:column wrap;
    width:100%;
    position:relative;
    scroll-snap-align: center ;
    background-image: url(/portfolio/static/media/contact.d92049e2.png);
    background-repeat: no-repeat;
    background-position: 10% 50%;
    padding-top:12%;
  
  }

  .con{
    margin-top:20px;
    text-indent: 50%;

  }

  #form{
    margin-left:50%;
  }

  #exampleText{
    width:600px;
    resize: none;
  }

  #exampleEmail{
    width:600px;
  }

  #mybutton{
    width:100px;
    margin-right:20px;
    margin-top: 10px;
    
  }

  #cancel{
    width:100px;
    margin-top: 10px;
    
  }

  @media (max-device-width: 800px){
    .Contact{
      scroll-snap-align: none;
      display:block;
      height:100vh;
      
    }
   
}
.Content{
   background: #43cea2;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
