.Nav nav ul{
    z-index:1;
    position:fixed;
    top:0;
    width:100%;
    height:60px;
    background-color: #185a9d;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.4);
}

.Nav nav ul li{
    list-style: none;
    border-radius: 50%;
    font-size: 20px;
    float:right;
    margin-top:10px;
}

.Nav nav ul li a{
    position: relative;
    text-decoration: none;
    color:white;
    margin:20px;
}

